import axios from "axios";
export default {
  data() {
    return {
      sighting: {},
    };
  },
  async created() {
    const config = {
      headers: {
        Accept: "application/json",
      },
    };

    try {
      const res = await axios.get(
        "http://localhost:3004/s/${this.$route.params.id}",
        config
      );

      this.sighting = res.data.sighting;
    } catch (err) {
      console.log(err);
    }
  },
};
